.typeInputList {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    padding-bottom: 10px;
    display: grid;
    gap: 6px;

    .TypeRow {
        width: 96%;
        display: grid;
        align-items: center;
        grid-template-columns: auto 1fr;
        gap: 4px;
        padding-right: 4%;

        .checkTypeContent {
            box-sizing: border-box;
            background-color: #ffffff;
            width: 30px;
            height: 30px;
            padding: 4px;
            border-radius: 25px;
            display: flex;
            justify-content: center;
            align-items: center;
            box-shadow: 1px 1px 2px #00000099;
        }
    }
}
