:root {
    --primary: #f2f2f2;
    --secondary: #4d6780;
    --tertiary: #324e67;
    --comp1: #dd5151;
    --comp2: #1c344a;

    --header1: #8eb1d2;
    --header2: #324e67;

    --primaryTitle: #324e67;
    --disablePrimaryTitle: #324e6799;
    --secondaryTitle: #4d6780;
    --tertiaryTitle: #ffffff;
    --text: #4d6780;
    --textFont: 0.8rem;
    --titleFont: 1.2rem;

    --button1: #e79533;
    --button2: #f3d156;
    --select: #f1b55a;
    --heart: #e94e70;
    --activeButton1: #edbf89;
    --activeButton2: #fef0be;
    --buttonTittle: #00000066;

    --bw100: #ffffff;
    --bw90: #e6e6e6;
    --bw80: #cccccc;
    --bw70: #b3b3b3;
    --bw60: #999999;
    --bw50: #808080;
    --bw0: #000000;

    --font_08: 0.8rem;
}

body {
    margin: 0;
    height: 100vh;
    background-color: var(--primary);
    font-family: "Roboto", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    overscroll-behavior-y: none;
    overflow: hidden;
}

#root {
    height: 100vh;
}

input {
    font-family: "Roboto", sans-serif;
}

a {
    text-decoration: none;
}

img {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

button {
    user-select: none;
}

.mt-1 {
    margin-top: 10px;
}

.mobile-600 {
    display: none;
}

.desktop-600 {
    display: grid;
}

/* width */
::-webkit-scrollbar {
    width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
    background-color: transparent;
    margin-bottom: 120px;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background-color: #bfbfbf;
    border-radius: 50px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background-color: #b3b3b3;
    border-radius: 50px;
}

@media only screen and (max-width: 600px) {
    .mobile-600 {
        display: flex;
    }

    .desktop-600 {
        display: none;
    }

    /* width */
    ::-webkit-scrollbar {
        width: 3px;
    }
}
